.multiple-choices {
  animation: pop-in 1s ease-out;
}

@keyframes pop-in {
  0% {
    transform: scale(0) translateY(-100px);
    opacity: 0;
  }

  80% {
    transform: scale(1.05) translateY(10px);
    opacity: 1;
  }

  100% {
    transform: scale(1) translateY(0px);
    opacity: 1;
  }
}

